"use server";

import { z } from "zod";
import { calculateProfitMargin } from "@/ai/flows/calculate-profit-margin";
import type { CalculateProfitMarginInput } from "@/ai/flows/calculate-profit-margin";

const inputSchema = z.object({
  productName: z.string().min(1, "Product name is required."),
  wholesalePrice: z.coerce.number({invalid_type_error: "Must be a number"}).min(0, "Wholesale price must be a positive number."),
  retailPrice: z.coerce.number({invalid_type_error: "Must be a number"}).min(0, "Retail price must be a positive number."),
  wholesaleSource: z.string().min(1, "Wholesale source is required."),
  retailSource: z.string().min(1, "Retail source is required."),
});

export async function getProfitMargin(values: CalculateProfitMarginInput) {
  const validatedInput = inputSchema.safeParse(values);
  if (!validatedInput.success) {
    return { error: "Invalid input.", details: validatedInput.error.flatten() };
  }

  try {
    const result = await calculateProfitMargin(validatedInput.data);
    return { success: true, data: result };
  } catch (error) {
    console.error("AI flow failed:", error);
    return { error: "Failed to calculate profit margin. Please try again." };
  }
}
