"use client";

import * as React from "react";
import { v4 as uuidv4 } from 'uuid';
import { useToast } from "@/hooks/use-toast";
import type { Calculation } from "@/lib/types";
import { Icons } from "@/components/icons";
import { ProfitCalculator } from "@/components/profit-calculator";
import { CalculationResult } from "@/components/calculation-result";
import { CalculationHistory } from "@/components/calculation-history";
import { getProfitMargin } from "@/app/actions";
import type { CalculateProfitMarginInput } from "@/ai/flows/calculate-profit-margin";

export default function Home() {
  const { toast } = useToast();
  const [isLoading, setIsLoading] = React.useState(false);
  const [currentCalculation, setCurrentCalculation] = React.useState<Calculation | null>(null);
  const [history, setHistory] = React.useState<Calculation[]>([]);

  React.useEffect(() => {
    try {
      const storedHistory = localStorage.getItem("marginwise-history");
      if (storedHistory) {
        setHistory(JSON.parse(storedHistory));
      }
    } catch (error) {
      console.error("Failed to parse history from localStorage", error);
      setHistory([]);
    }
  }, []);

  const handleNewCalculation = async (values: CalculateProfitMarginInput) => {
    setIsLoading(true);
    const result = await getProfitMargin(values);
    setIsLoading(false);

    if (result.success && result.data) {
      const newCalculation: Calculation = {
        id: uuidv4(),
        createdAt: new Date().toISOString(),
        ...values,
        ...result.data,
      };
      setCurrentCalculation(newCalculation);
      const updatedHistory = [newCalculation, ...history];
      setHistory(updatedHistory);
      localStorage.setItem("marginwise-history", JSON.stringify(updatedHistory));
      toast({
        title: "Calculation Complete",
        description: `Successfully analyzed ${values.productName}.`,
      });
    } else {
      toast({
        variant: "destructive",
        title: "Error",
        description: result.error || "An unexpected error occurred.",
      });
    }
  };

  const handleSelectHistory = (calculation: Calculation) => {
    setCurrentCalculation(calculation);
  };

  const handleClearHistory = () => {
    setHistory([]);
    setCurrentCalculation(null);
    localStorage.removeItem("marginwise-history");
    toast({
      title: "History Cleared",
      description: "Your calculation history has been removed.",
    });
  };

  return (
    <div className="min-h-screen w-full">
      <header className="sticky top-0 z-10 w-full border-b bg-background/80 backdrop-blur-md">
        <div className="container mx-auto flex h-16 items-center justify-between px-4 md:px-6">
          <div className="flex items-center gap-2">
            <Icons.Logo className="h-8 w-8 text-primary" />
            <h1 className="font-headline text-2xl font-bold text-primary">MarginWise</h1>
          </div>
          <p className="hidden text-sm text-muted-foreground md:block">
            Smart Profit Analysis for Smart Businesses
          </p>
        </div>
      </header>
      <main className="container mx-auto p-4 md:p-6">
        <div className="grid grid-cols-1 gap-6 lg:grid-cols-5">
          <div className="flex flex-col gap-6 lg:col-span-2">
            <ProfitCalculator
              onSubmit={handleNewCalculation}
              isLoading={isLoading}
            />
            <CalculationHistory
              history={history}
              onSelect={handleSelectHistory}
              onClear={handleClearHistory}
              selectedId={currentCalculation?.id}
            />
          </div>
          <div className="lg:col-span-3">
            <CalculationResult calculation={currentCalculation} isLoading={isLoading} />
          </div>
        </div>
      </main>
      <footer className="container mx-auto mt-auto py-6 px-4 text-center text-sm text-muted-foreground md:px-6">
        <p>&copy; {new Date().getFullYear()} MarginWise. All Rights Reserved.</p>
      </footer>
    </div>
  );
}
